import React from "react";
import { Routes, Route, Link } from "react-router-dom";
import News from "./News";
import blog from "../assets/blog1.jpg";
const Knowlege = () => {
  return (
    <React.Fragment>
      <header className="know_header">
        <div className="image" />
        <div className="path_desc">
          <h2>
            <span>/</span>      
                     .
          </h2>
        </div>
      </header>
      <div className="inner_routes_holder">
        <Routes>
          <Route
            path="/"
            element={
              <section className="blogs_grid">
                <div className="blog">
                  <img src={blog} alt="blog1" />
                  <div className="laer">
                    <h4>    </h4>
                    <Link to="/knowlege/time-importance">{`  <<`}</Link>
                    <span className="line" />
                    <span className="date">10 -  - 2023</span>
                  </div>
                </div>
              </section>
            }
          />
          <Route path=":time-importance" element={<div />} />
        </Routes>
      </div>
      <News />
    </React.Fragment>
  );
};

export default Knowlege;
