import React, { useRef, useState } from "react";
import Logo from "../assets/logo.png";
import { NavLink, Link } from "react-router-dom";
const Nav = () => {
  const drop = useRef(null);
  const nav = useRef(null);
  const search = useRef(null);
  const [toggle, setToggle] = useState(false);
  const handlemove = () => {
    drop.current.style.height = "270px";
  };
  const handleLeave = () => {
    drop.current.style.height = "0px";
  };
  window.addEventListener("scroll", () => {
    if (window.scrollY >= 100) {
      nav.current.classList.add("active");
    } else {
      nav.current.classList.remove("active");
    }
  });
  const handleclick = () => {
    if (toggle === false) {
      search.current.style.width = "150px";
      setToggle(true);
    } else {
      search.current.style.width = "0";
      setToggle(false);
    }
  };
  return (
    <nav ref={nav}>
      <div className="logo">
        <Link to="/">
          <img src={Logo} alt="logo" />
        </Link>
      </div>
      <ul className="nav-items">
        <li className="nav-item">
          <NavLink className="nav-link" to="/">
            
          </NavLink>
        </li>
        <li className="nav-item">
          <NavLink
            to="/services"
            onMouseMove={handlemove}
            onMouseLeave={handleLeave}
            className="icon nav-link"
          >
             <i className="fa-solid fa-caret-down" />
          </NavLink>
          <div
            ref={drop}
            onMouseMove={handlemove}
            onMouseLeave={handleLeave}
            className="drobdown"
          >
            <ul>
              <li>
                <Link to="/services">  </Link>
              </li>
              <li>
                <Link to="/services/legal-advices"> </Link>
              </li>
              <li>
                <Link to="/services/administrative-advices">
                   
                </Link>
              </li>
              <li>
                <Link to="/services/business-services"> </Link>
              </li>
              <li>
                <Link to="/services/foreign-investments">
                   
                </Link>
              </li>
              <li>
                <Link to="/services/investment-opportunities">
                   
                </Link>
              </li>
              <li>
                <Link to="/services/preperaing-work-plans">
                    
                </Link>
              </li>
              <li>
                <Link to="/services/feasibility-study-for-financing-agencies">
                     
                </Link>
              </li>
            </ul>
          </div>
        </li>
        <li className="nav-item">
          <NavLink className="nav-link" to="/clients">
            
          </NavLink>
        </li>
        <li className="nav-item">
          <NavLink className="nav-link" to="/employment">
            
          </NavLink>
        </li>
        <li className="nav-item">
          <NavLink className="nav-link" to="/knowlege">
            
          </NavLink>
        </li>
        <li className="nav-item">
          <NavLink className="nav-link" to="/contact">
             
          </NavLink>
        </li>
      </ul>
      <div className="search-lang">
        <input type="text" placeholder="  " ref={search} />
        <button onClick={handleclick}>
          <i className="fa-solid fa-magnifying-glass" />
        </button>
        <span />
        <button>EN</button>
      </div>
    </nav>
  );
};

export default Nav;
