import React from "react";
import vector from "../assets/Vector.png";
const News = () => {
  return (
    <section className="news">
      <div className="label">
        <p>          </p>
      </div>
      <form action="">
        <input
          type="email"
          required
          name="email"
          placeholder=" "
        />
        <button type="submit">
          <img src={vector} alt="send" />
        </button>
      </form>
    </section>
  );
};

export default News;
